.App {
    text-align: center;
    
  }
  
  
  
  .App-header {
    background-color: #222;
    height: 100px;
    padding: 20px;
    color: white;
    position: fixed;
    width: 100%;
  }
  
  .App-title {
    font-size: 1.5em;
  }
  
  .App-intro {
    font-size: large;
  }
  .inputText{
      width: 100%;
  }
  .listColor{
      color: black
  }
  @-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  @keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  


